.main-header {
  font-weight: 700;
  font-size: 17px;
  line-height: 24px;
  margin: 0 auto;
  background-color: #ffffff;
  position: relative;
  box-shadow: 0 1px 0 hsl(0, 0%, 93%), inset 0 1px 0 hsl(0, 0%, 93%);
}

.main-header__wrapper {
  max-width: 1150px;
  margin: 0 auto;
}

.main-header__logo {
  max-height: 79px;
  min-width: 260px;
  text-align: center;
  margin-top: 22px;
  margin-bottom: 22px;
  padding-left: 30px;
  padding-right: 30px;
}

.main-header__logo:hover {
  opacity: 0.5;
}

.main-header__logo:active {
  opacity: 0.3;
}

.main-header__text {
  display: none;
}

@media (min-width: $tablet-width) {
  .main-header {
    flex-direction: row;
    height: 88px;
  }

  .main-header__logo {
    min-height: 67px;
    margin: 0;
    padding-left: 50px;
    padding-right: 0;
    padding-top: 22px;
    text-align: start;
    min-width: 321px;
  }

  .main-header__text {
    display: block;
    font-weight: 400;
    margin: 0;
    order: 2;
    padding-right: 50px;
    padding-top: 19px;
  }
}

@media (min-width: $desktop-width) {
  .main-header__logo {
    order: 1;
    padding: 0;
    text-align: center;
  }
}
